import React, { useState, useEffect } from 'react';
import './MapPreview.css';
import MapRenderer from './MapRenderer';

const MapPreview = ({ map, onDownload, onShare }) => {
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setIsLoading(true);
  }, [map.diagram]);

  const handleDiagramLoad = () => {
    setIsLoading(false);
  };

  return (
    <div className="map-preview">
      <h3>{map.title}</h3>
      <div className="map-container">
        {isLoading && (
          <div className="loading-indicator">
            <div className="loading-spinner"></div>
            <p>Loading diagram...</p>
          </div>
        )}
        <div style={{ opacity: isLoading ? 0 : 1 }}>
          {map.diagram ? (
            <MapRenderer
              diagram={map.diagram}
              onRendered={handleDiagramLoad}
            />
          ) : map.image ? (
            <img
              src={map.image}
              alt={map.title}
              className="map-image"
              onLoad={handleDiagramLoad}
            />
          ) : (
            <div className="error-message">No diagram or image available</div>
          )}
        </div>
      </div>
      <div className="map-actions">
        <button onClick={() => onDownload(map)}>Download</button>
        <button onClick={() => onShare(map)}>Share</button>
      </div>
    </div>
  );
};

export default MapPreview;
