import React, { useEffect, useRef, useState } from 'react';
import './MapRenderer.css';

const MapRenderer = ({ diagram }) => {
  const containerRef = useRef(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const renderDiagram = async () => {
      if (!diagram || !containerRef.current) return;

      // Wait for mermaid to be available
      const waitForMermaid = () => {
        return new Promise((resolve) => {
          const check = () => {
            if (window.mermaid) {
              resolve(window.mermaid);
            } else {
              setTimeout(check, 100);
            }
          };
          check();
        });
      };

      try {
        setIsLoading(true);
        const mermaid = await waitForMermaid();

        // Clear previous content
        containerRef.current.innerHTML = '';

        // Create a unique ID for this diagram
        const id = `mermaid-${Math.random().toString(36).slice(2)}`;

        // Create container with the diagram
        const diagramContainer = document.createElement('div');
        diagramContainer.className = 'mermaid';
        diagramContainer.id = id;
        diagramContainer.textContent = diagram;

        // Add to DOM
        containerRef.current.appendChild(diagramContainer);

        // Initialize mermaid
        mermaid.initialize({
          startOnLoad: true,
          theme: 'default',
          securityLevel: 'loose',
          flowchart: {
            htmlLabels: true,
            curve: 'basis',
          },
        });

        // Render the diagram
        await mermaid.run();
        setIsLoading(false);
      } catch (error) {
        console.error('Error rendering diagram:', error);
        if (containerRef.current) {
          containerRef.current.innerHTML = `
            <div class="error-message">
              <p>Failed to render diagram</p>
              <button onclick="window.location.reload()">Retry</button>
            </div>
          `;
        }
        setIsLoading(false);
      }
    };

    renderDiagram();
  }, [diagram]);

  return (
    <div className="diagram-wrapper">
      {isLoading && (
        <div className="loading-overlay">
          <div className="loading-spinner"></div>
          <p>Loading diagram...</p>
        </div>
      )}
      <div ref={containerRef} className="diagram-container" />
    </div>
  );
};

export default MapRenderer;
